@media (min-width: 1200px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl {
    max-width: 1240px;
  }
}
#slider-topo {
  width: 100%;
}
#slider-topo .slider-item img,
#slider-topo .slider-item video {
  width: 100%;
  height: 100vh;
  object-fit: cover;
  object-position: top;
}
#slider-topo .container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
#slider-topo .container .slider-text {
  width: 30%;
}
#slider-topo .container .slider-text h2 {
  font-size: 30px;
  line-height: 37px;
  font-weight: bold;
  color: #cae098;
}
#slider-topo .container .slider-text h2::first-line,
#slider-topo .container .slider-text p {
  line-height: 20px;
  color: #fff;
}
#slider-topo .container .slider-text p {
  margin-bottom: 0;
}
#slider-topo .container a {
  margin-top: 20px;
}